@media screen and (min-width: 700px) {
	.site-branding .site-branding-text .site-description h2 {
		color: #fff;
		text-shadow: 0;
	}
}

@media screen and (min-width: 900px) {
	.site-branding .site-branding-text .site-description h2 {
		font-size: 1.25rem;
	}
}

.site-branding .site-branding-text .site-description .header-buttons {
