//intro 3 offset pictures
.intro-imgs {
    display: block;

    @media screen and (min-width: 769px) {
        display: flex;
    }

    .img-1 {
        width: 100%;
        margin-bottom: 2px;

        @media screen and (min-width: 769px) {
            margin: 0 2px 0 0;
            width: 60%;
            flex: .6;
        }

    }

    .img-2 {
        width: 100%;
        display: block;

        @media screen and (min-width: 569px) {
            display: flex;

        }

        @media screen and (min-width: 769px) {
            display: block;
            width: 40%;
            flex: .4;
        }

        .one-of-two {
            margin-bottom: 3px;            
            
            @media screen and (min-width: 569px) {
                margin: 0;
                padding-right: 2px;
            }

            @media screen and (min-width: 769px) {
                margin: 0 0 2px 0;
                padding: 0;
            }
        }

        img {
            width: 100%;
        }
    }

    .img-container {
        position: relative;

        img {
            display: block;
        }

        .link-container {
            width: 100%;
            text-align: center;
            position: absolute;
            top: 75%;

            @media screen and ( min-width: 768px ) {
                top: 65%;
            }

            @media screen and ( min-width: 1025px ) {
                top: 75%;
            }
        
            a {
                font-family: $font__sans;
                font-size: 12px;
                font-weight: 700;
                display: inline-block;
                padding: 8px 20px 8px;
                border-radius: 1000px;
                border: 1px solid #000;
                text-decoration: none;
                background: #000;
                color: #1d9d73;
                transition: all linear .25s;

                @media screen and ( min-width: 768px ) {
                    padding: 10px 16px 10px;
                    font-size: 14px;
                }

                @media screen and ( min-width: 1025px ) {
                    padding: 14px 20px 14px;
                    font-size: 16px;
                }

                &:hover,
                &:focus,
                &:active {
                    animation: hvr-bob-float .3s ease-out 1 forwards normal, hvr-bob 1.5s .3s ease-in-out infinite forwards alternate;
                }
            }
        }
    }
}


//featured products 
.best-sellers {

    h2 {
        margin: 0;
        padding: .75em 0;
        font-size: 1.5rem;
        text-align: center;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            height: 5px;
            border-top: 1px solid #e6e6e6;
            bottom: 44px;
            width: 100%;
            z-index: 0;
            left: 0;
        }
        
        @media screen and ( min-width: $query__small ) {
            padding: 1em 0;
            word-spacing: .25em;
            font-size: 1.75rem;
        }

        span {
            padding: 0 40px;
            line-height: 1em;
            background: #fff;
            position: relative;
        }
    }

    .columns-5 {

        .products {
            margin: 0;
            padding: 0 .5em;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;

            @media screen and ( min-width: 1025px ) {
                display: block;
            }

            .quantity {
                display: none;
            }

            .product  {
                flex-basis: 45%;

                @media screen and ( min-width: 550px ) {
                    flex-basis: 30%;
                }

                @media screen and ( min-width: 769px ) {
                    flex-basis: 25%;
                }
            
                img {
                    border: none;
                    text-decoration: none;
                    
                    &:hover {
                        transform: scale(1.05);
                    }
                }

                .woocommerce-loop-product__link:hover img {
                    border: none;
                }
            }
        }
    }
}


//about us animation
.about-us-snippet {
    display: block;
    color: #333;

    @media screen and ( min-width: $query__small ) {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    img {
        width: 100%;

        @media screen and ( min-width: $query__small ) {
            width: 45%;
        }

        @media screen and ( min-width: 1025px ) {
            width: 500px;
        }
    }

    .about-snippet-text {
        width: 95%;
        margin: 0 auto;
        background-color: rgba(255, 255, 255, 0.8);
        padding: 1em;
        position: relative;
        
        @media screen and ( min-width: $query__small ) {
            width: 45%;
            right: 7.5%;
            margin: 0;
        }

        @media screen and ( min-width: 1025px ) {
            width: 500px;
        }

        h4 {
            font-family: $font__sans;
            font-size: 1.25rem;
            color: $color__site-brown-dark;

            @media screen and ( min-width: 500px ) {
                font-size: 1.15rem;
            }
        }

        p {
            font-family: Helvetica, Arial, "Lucida Grande", sans-serif;
            font-size: 1.05rem;

            @media screen and ( min-width: 500px ) {
                font-size: 1rem;
            }
        }

        a {
            font-family: Helvetica, Arial, "Lucida Grande", sans-serif;
            font-size: 1.1rem;
            color: #fff;
            border-radius: 2px;
            padding: 8px 18px;
            background: $color__site-brown-dark;

            @media screen and ( min-width: $query__small ) {
                font-size: 1.15rem;
            }

            span {
                position: relative;
                right: 0;
                transition: all .2s ease-out;
            }

            &:hover {

                span {
                    right: 2px;
                }

                i {
                    opacity: 1;
                    left: 2px;
                }
            }
        }

        i {
            padding: 0 4px;
            opacity: 0;
            position: relative;
            left: 0;
            font-size: .9rem;
            color: #fff;
            transition: all .2s ease-out;
        }
    }
}


//blog headliner
.blog-headline {
    background: url( http://tribalurban.dev/wp-content/uploads/2017/11/sugar-scrub.jpg ) no-repeat center center;
    background-attachment: fixed;
    min-height: 200px;
    position: relative;
    overflow: hidden;
    margin-top: 2em;

    @include dark-background( $background-color: rgba(0,0,0,.5) );

    @media screen and (min-width: $query__small) {
        @include slanted-background;
        min-height: 400px;
    }

    .blog-info {
        color: #fff;
        font-family: $font__sans;
        position: relative;
        z-index: 1;
        padding: 1em;
        width: 100%;
        margin-left: auto;
        text-align: center;

        @media screen and ( min-width: $query__small ) {
            width: 50%;
        }

        @media screen and ( min-width: 1025px ) {
            padding: 2em;
        }

        p {
            margin: 0 0 1em;
            font-size: 1.25rem;

            @media screen and ( min-width: 769px ) {
                font-size: 1.5rem;
            }
        }

        a {
            font-family: $font__sans;
            font-size: 12px;
            font-weight: 700;
            display: inline-block;
            padding: 8px 20px;
            border-radius: 1000px;
            border: 1px solid #fff;
            text-decoration: none;
            background: transparent;
            color: #fff;
            transition: all linear .25s;

            @media screen and ( min-width: 769px ) {
                padding: 10px 30px;
                font-size: 14px;
            }

            @media screen and ( min-width: 1025px ) {
                padding: 14px 40px;
                font-size: 16px;
            }

            &:hover {
                background: #000;
                border-color: #000;
            }
        }

    }
}